.section__newsMain {
	position: relative;
	width: 100%;
	height: auto;
	padding-top: 60px;
	background: $lightBg;
	padding-bottom: 115px;
	@include max(lg) {
		padding-top: 40px;
		.layoutTwo__left {
			justify-content: flex-start;
			align-items: flex-start;
		}
	}

	@include max(lg) {
		.layoutTwo__left {
			flex-flow:  row nowrap;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}
	@media (max-width:801px) {
		.layoutTwo__left {
			flex-flow:  column nowrap;
			justify-content: flex-start;
			align-items: flex-start;
		}
	}
}

.newsMain__title {
	font-family: 'Manrope';
	font-style: normal;
	font-weight: 800;
	font-size: $fz32;
	line-height: $lh40;
	color: #031B4B;
}

.newsMain__title__mob  {
	display: none;
	margin-bottom: 32px;
}
.newsMain__title__desc {
	display: block;
	margin-bottom: 40px;
}
@include max(lg) {
	.layoutTwo {
		flex-flow: column nowrap;
	}
	.layoutTwo__left {
		margin-bottom: 60px;
		flex-flow: row nowrap;
		justify-content: flex-end;
		margin-right: 0;
		flex: 1 0 auto;
		align-items: flex-start;
	}
	.newsMain__title__mob  {
		display: block;
		margin-bottom: 36px;
	}
	.newsMain__title__desc {
		display: none;
	}
	.periodSelect {
		flex: 1 1 auto
	}
	.followSoc {
		flex: 1 0 199px
	}
}


@include max(md) {
	.layoutTwo__left {
		margin-bottom: 60px;
	}
	.layoutTwo__left {
		flex-flow: column nowrap;
	}
}
 